@mixin base-button {
	background: transparent;
	border: 0;
	cursor: pointer;
	display: inline-block;
	font-size: 1rem;
	line-height: 2.75rem;
	min-width: 180px;
	outline: none;
	text-align: center;
	text-decoration: none;
	transition: 0.12s ease-in-out;
}

.button {
	@include base-button;
	color: var(--regular-white);
	background: var(--new-blue);
	border-radius: 30px;

	a {
		display: block;
	}

	&.button--small {
		font-size: var(--font-size-small);
		line-height: 42px;
		min-width: 0;
	}

	&.button--primary {
		background: var(--ocean-blue);

		&:hover,
		&:focus {
			background: var(--light-ocean-blue);
		}
	}
}

.button-link {
	@include base-button;
	text-align: left;
	display: block;
  align-items: center;
	min-width: 0;

	img {
		margin-left: var(--gutter-small);
		vertical-align: middle;
	}

	&--primary {
		color: var(--fall-orange);
	}

	&--invert {
		color: var(--regular-white);
	}
}
